<template>
  <div class="music">
    <h1 class="mt-6 text-2xl font-bold light-mode:text-gray-900 leading-tight sm:mt-8 sm:text-4xl lg:text-3xl xl:text-4xl">Music</h1>
    <div class="mx-auto py-5">
        Music i'm listening to now
        <ul id="tracks">          
          <li v-for="(song, index) in sResults" :key="index">
            <div class="m-6 rounded-lg light-mode:bg-white bg-black mx-8 max-w-sm shadow-lg rounded-lg overflow-hidden">
                <div class="sm:flex sm:items-center px-6 py-4">
                    <img class="block h-16 sm:h-24 rounded-lg mx-auto mb-4 sm:mb-0 sm:mr-4 sm:ml-0" :src="song.artworkUrl100" alt="no art">
                    <div class="text-center sm:text-left sm:flex-grow">
                        <div class="mb-4">
                            <p class="text-xl leading-tight">{{song.artistName}}</p>
                            <p class="text-sm leading-tight text-grey-dark">{{song.trackName}}</p>
                        </div>
                        <div>
                          <audio :id="`T_${song.trackId}`" preload="none">
                            <source :src="song.previewUrl" type="audio/mp4" />
                            <p>Your browser does not support HTML5 audio.</p>
                          </audio>
                        </div>
                        <div>
                          <button class="hover:text-pink-600" v-for="(control, index) in audioControls" :key="index" @click="audioControl(song.trackId, control)">
                            <font-awesome-icon class="min-w-16 my-0 mx-3" :icon="['fas', control]" />
                          </button>                          
                        </div>
                    </div>
                </div>
            </div>
          </li>
        </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return{

      /* note
        should put this song list in the store
        and then update store with extra data from query
        that way if someone clicks away and comes back to component
        we can first check if store has all data. then we don't need 
        to query api.
        Using localstorage for now. not sure if need to use the store if it's jsut for this component
      */
      audioControls:['play','pause', 'volume-up', 'volume-down'],
      sResults:[],
      songList:[        
        {          
          artist:'Heroes For Hire',
          name: 'Bright Lights In Paradise'          
        },
        {          
          artist:'Four Year Strong',
          name: 'Learn to Love the Lie'
        },
        {          
          artist:'Mayday Parade',
          name: 'It Is What It Is'
        },
        {          
          artist:'Chief State',
          name: 'Reprise'
        },
        {          
          artist:'2Pac',
          name: 'Do for Love',          
        },
        {          
          artist:'Life Looks Good',
          name: 'Connecticut',          
        },
        {          
          artist:'Thief Club',
          name: 'Jen',          
        },
        {          
          artist:'Chief State',
          name: 'Broken Eyes',          
        },
        {          
          artist:'Knuckle Puck',
          name: 'Tune You Out',          
        },
        {
          /*artistId: 730370680,*/
          artist:'Thief Club',
          name: 'Chicago',          
        },
        {          
          artist:'All Time Low',
          name: 'Sleeping In',          
        },
        {          
          artist:'50 Cent',
          name: 'How To Rob (feat. Madd Rapper)',          
        }        
      ]
    }
  },
  created: function() {        
    //console.log(`created @ ${new Date().toString()}\n`) // useful for debugging
    // let check if data exists in local storage
    let _this = this; // need to save this for access in arrow functions
    if (window.localStorage.getItem("andy-around-music-data") === null) {
      //console.log('No Local Data Found. Fetching from API \n');      
      //array map the artist-track array using promise so we know when all data has been fetched.
      const actions = this.$data.songList.map(this.runSearch);
      // once all the promises resolve our results array should be complete so we can store this
      Promise.all(actions).then(() => {
        window.localStorage.setItem('andy-around-music-data', JSON.stringify(_this.sResults));
      });
    } else {
      // Data was found locally
      //console.log('Local Data Found. Using this \n');
      let lData = JSON.parse(window.localStorage.getItem("andy-around-music-data"));
      if (lData.length ==0) {
        localStorage.removeItem('andy-around-music-data');
        window.location.reload();
      } else {
        this.sResults = lData;
        // need to verify that all the tracks exist
        // also if local data contains an old track we should remove this
        // should query for tracks that aren't in local data
        // and once we have done all that (data should be up to date)
        // we should store this new data back into localdata        
        let missingData = this.checklocalData(lData);
        //console.log(missingData);
        //array map the missing data array using promise so we know when all data has been fetched.
        const actions = missingData.map(this.runSearch);
        // once all the promises resolve our results array should be complete so we can store this
        Promise.all(actions).then(() => {
          window.localStorage.setItem('andy-around-music-data', JSON.stringify(_this.sResults));
        });      
      }      
    }
  },
  methods: {
    getMusicData: function(search, type) {
      let sURL = 'https://itunes.apple.com/';
          sURL += (type) ? `lookup?id=${search}` : `search?term=${search}&media=music`
          sURL += '&entity=song'
      return axios({
        method: 'get',
        mode: 'no-cors',
        url: sURL,
        responseType: 'json'      
      })
      .then(response=>response.data)
    .catch(e =>{ 
      window.localStorage.setItem('andy-around-music-data', JSON.stringify(e));
    })
    },
    runSearch: function (searchParams) {      
      let _this = this; // save this for access inside arrow functions
      let search = searchParams.artist+' '+searchParams.name, // default search is for artist and song name
          idSearch = false;
      if ("artistId" in searchParams) {
        search = searchParams.artistId;                  // if we have artist id we can do lookup. need to work out how to get specific song though
        idSearch = true;
      }                 
      return this.getMusicData(search, idSearch).then(res => {
        res.results.forEach(s => {
          if (s.trackName == searchParams.name && s.artistName == searchParams.artist) {
            _this.sResults.push(s); //if the search result is the track we're after add it to the results array
          }          
        });
      });                            
    },
    checklocalData: function (dataToCheck) {
      let _this = this;
      let filteredArry = _this.songList.filter(
        (el) => {
          let songFound = false; // default that song isn't in local data
          for (var i = 0; i < dataToCheck.length; i++) { // iterate over filter
            let sA = el.artist.toLowerCase(),
                sN = el.name.toLowerCase(),
                lA = dataToCheck[i].artistName.toLowerCase(),
                lN = dataToCheck[i].trackName.toLowerCase();            
            if (((sA == lA) && (sN == lN))) {                 
              songFound =  true; // song was found somewhere in localdata array
            }
          }
          return !songFound; // only want to send back if song wasn't found
        }
      );
    return filteredArry;
    },
    audioControl: function(elm,ctrl){
      //console.log(event);
      //pass in element id and control (play/pause/etc.)
      let a = document.getElementById(`T_${elm}`);
      //console.log(`elm: ${elm} ctrl: ${ctrl}`);
      switch (ctrl){
        case "play": 
          a.play();
          event.target.parentElement.classList.add('text-green-500');
          break;
        case "pause": 
          a.pause();
          break;
        case "volume-up": 
          a.volume += 0.1;
          break;
        case "volume-down": 
          a.volume -= 0.1;
          event.target.parentElement.classList.add('text-green-500');
          break;
      }
    }
  }
}
</script>